import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import store from './store/index.js'


import Vue from 'vue'
Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
    ...App,
	store
})
Vue.filter('formatCount',function(value){
	if(value >= 10000 && value <= 100000000){
		value /= 1000;
		return value.toFixed(1) + '万';
	}else if(value > 100000000){
		value /= 100000000;
		return value.toFixed(1) + '亿';
	}else{ 
		return value
	}
});

app.$mount()